import React, { Component } from 'react';
import Axios from 'axios';

import Header from './components/layout/Header';
import ContactList from './components/ContactList';
import Contact from './components/Contact'

import './styles/App.scss';

export class App extends Component {
  state = {
    contacts: [
      {
        "id": 1,
        "email": "jaydon_gislason59@yahoo.com",
        "phone": "(998) 970-2799",
        "address": {
          "city": "South Nyah",
          "state": "New York",
          "country": "Equatorial Guinea",
          "zipCode": "16308-9260",
          "countryCode": "AX",
          "streetAddress": "3585 Lucinda Valleys"
        },
        "lastName": "Von",
        "firstName": "Walter",
        "profileImage": "https://s3.amazonaws.com/uifaces/faces/twitter/jeremyworboys/128.jpg"
      },
    ],
    selectedContactId: 1,
    searchResult: [],
  }

  componentDidMount() {
    Axios.get('http://mock-io.herokuapp.com/users')
      .then( response => this.setState({
        contacts: response.data,
      }))
  }
  
  showContact = (id) => {
    this.setState({
      selectedContactId: id
    })
  }
  
  getContact = (id) => {
    return this.state.contacts.filter(contact => contact.id === id)[0]
  }

  searchContacts = (query) => {
    if(query.length > 0){
      return this.state.contacts.filter(contact => contact.firstName.includes(query));
    }
    return [];
  }

  render() {
    return (
      <div className="app">
        
        <Header 
          contacts = { this.state.contacts }
          searchContacts = { this.searchContacts }
          searchResult = { this.searchContacts }
        />

        <ContactList
          contacts = { this.state.contacts }
          showContact = { this.showContact }
        />

        <Contact 
          contact={ this.state.selectedContactId > 0 ? this.getContact(this.state.selectedContactId) : this.getContact(1)}
        />


      </div>
    )
  }

}

export default App
